.loginsignup{
    width: 100%;
    height: 70vh;
    background: linear-gradient(180deg,#ff4141,#f1ebf3 62%);
    padding-top: 50px;
    
}
.loginsignup-container{
    width: 400px;
    height: 600px;
    background: white;
    margin: auto;
    padding: 40px 60px;
    padding-bottom: 5px;
    
}
.loginsignup-container h1{
    
    margin: 10px 0px;
    text-align: center;
    position: relative;
    top:0px;
    }


.loginsignup-fields{
    display: flex;
    flex-direction: column;
    gap: 29px;
    margin-top: 30px;
}
.loginsignup-fields input{
    height: 72px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid #c9c9c9;
    outline: none;
    color: #5c5c5c;
    font-size: 18px;
}
.loginsignup-container button{
    width: 300px;
    height: 72px;
    color: white;
    background: #ff4141;
    margin-top: 30px;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    border-radius: 50px;
    font-weight: 400;
    position: relative;
    left: 40px;
   
}
.loginsignup-login{
    margin-top: 20px;
    color:#5c5c5c;
    font-size:18px;
    font-weight: 500;
}
.loginsignup-login span{
    color:#ff4141;
    font-weight:600;
}
.loginsignup-agree{
    display: flex;
    align-items: center;
    margin-top:25px;
    gap:20px;
    color:#5c5c5c;
    font-size: 18px;
    font-weight: 500;
}