@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.hero{
    height: 100vh;
    background: linear-gradient(180deg, #eaa5ee 0%, #e1ffea22 60%);
    display : flex;

}
.hero-left{
    flex:1;
    display:flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left: 180px;
    line-height: 1.1;
}
.hero-left h2{
    color: #090909;
    font-size: 26px;
    font-weight:600;
    font-family: "Poppins", sans-serif;
}
.hero-left p{
    color:#171717;
    font-size: 100px;
    font-weight: 700;
    font-family: "Poppins", sans-serif;
}
.hero-hand-icon{
    display: flex;
    align-items: center;
    gap: 20px;
}
.hero-hand-icon img{
    width: 105px;
   
}
.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius:75px;
    margin-top: 30px;
    background:#d63bdf;
    color:white;
    font-size: 22px;
    font-weight: 500px;
}
.hero-right{
    right: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

